/**
 * Returns a function that clamps a number between 0 and the given maximum value.
 * @currying clamp(LargestNumber)(numberToBeClamped)
 * @param {number} max - The maximum value that the returned function will clamp values to.
 * @param {number} n - The number to be clamped
 * @return {number}
 */
export const clamp = (max: number) => (n: number) =>
  Math.min(max, Math.max(0, n));

export type TooltipPosition =
  | "top-left"
  | "top-right"
  | "bottom-left"
  | "bottom-right"
  | "top"
  | "bottom"
  | "left-top"
  | "left"
  | "left-bottom"
  | "right-top"
  | "right"
  | "right-bottom";

export function convertAnchor(
  rect: DOMRect,
  position: TooltipPosition | "top" | "bottom" | "left" | "right" = "bottom",
  align: "start" | "center" | "end" = "center",
  gap: number = 0
): React.CSSProperties {
  // Handle compound positions
  if (position === "top-left") return convertAnchor(rect, "top", "start", gap);
  if (position === "top-right") return convertAnchor(rect, "top", "end", gap);
  if (position === "bottom-left")
    return convertAnchor(rect, "bottom", "start", gap);
  if (position === "bottom-right")
    return convertAnchor(rect, "bottom", "end", gap);
  if (position === "left-top") return convertAnchor(rect, "left", "start", gap);
  if (position === "left-bottom")
    return convertAnchor(rect, "left", "end", gap);
  if (position === "right-top")
    return convertAnchor(rect, "right", "start", gap);
  if (position === "right-bottom")
    return convertAnchor(rect, "right", "end", gap);

  const vw = window.innerWidth ?? document.body.clientWidth;
  const vh = window.innerHeight ?? document.body.clientHeight;
  switch (position) {
    case "top":
      switch (align) {
        case "start":
          return { left: rect.x, bottom: clamp(vh)(vh - rect.y + gap) };
        case "center":
          return {
            left: clamp(vw)(rect.x + rect.width / 2),
            transform: "translateX(-50%)",
            bottom: clamp(vh)(vh - rect.y + gap),
          };
        case "end":
          return {
            right: clamp(vw)(vw - rect.x - rect.width),
            bottom: clamp(vh)(vh - rect.y + gap),
          };
        default:
          return {};
      }
    case "bottom":
      switch (align) {
        case "start":
          return { left: rect.x, top: clamp(vh)(rect.y + rect.height + gap) };
        case "center":
          return {
            left: clamp(vw)(rect.x + rect.width / 2),
            transform: "translateX(-50%)",
            top: clamp(vh)(rect.y + rect.height + gap),
          };
        case "end":
          return {
            right: clamp(vw)(vw - rect.x - rect.width),
            top: clamp(vh)(rect.y + rect.height + gap),
          };
        default:
          return {};
      }
    case "left":
      switch (align) {
        case "start":
          return {
            right: clamp(vw)(vw - rect.x + gap),
            top: rect.y,
          };
        case "center":
          return {
            right: clamp(vw)(vw - rect.x + gap),
            transform: "translateY(-50%)",
            top: clamp(vh)(rect.y + rect.height / 2),
          };
        case "end":
          return {
            right: clamp(vw)(vw - rect.x + gap),
            bottom: clamp(vh)(vh - rect.y - rect.height),
          };
        default:
          return {};
      }
    case "right":
      switch (align) {
        case "start":
          return {
            left: clamp(vw)(rect.x + rect.width + gap),
            top: rect.y,
          };
        case "center":
          return {
            left: clamp(vw)(rect.x + rect.width + gap),
            transform: "translateY(-50%)",
            top: clamp(vh)(rect.y + rect.height / 2),
          };
        case "end":
          return {
            left: clamp(vw)(rect.x + rect.width + gap),
            bottom: clamp(vh)(vh - rect.y - rect.height),
          };
        default:
          return {
            left: clamp(vw)(rect.x + rect.width + gap),
            top: rect.y,
          };
      }
  }
}
