import { useEffect, useRef, useState } from "react";
import styled, { css, useTheme } from "styled-components";
import { convertAnchor, TooltipPosition } from "../utils/convertAnchor";
import { TooltipPoint } from "../icons";
export interface TooltipProps {
  tooltip?: React.ReactNode;
  parent: React.RefObject<HTMLButtonElement | HTMLDivElement | null>;
  /**
   * @default bottom
   */
  tooltipPosition?: TooltipPosition;
  show?: boolean;
  style?: React.CSSProperties;
  /** @default 4 */
  wrapperGap?: number;
  bubbleColor?: string;
  /** @default {width:5,height:4} */
  bubbleSize?: { width: number; height: number };
  /** @default 12 */
  bubbleOffset?: number;
}

const gap = 4;

export function Tooltip(props: TooltipProps) {
  const {
    tooltip,
    parent,
    tooltipPosition = "bottom",
    show,
    bubbleSize = { width: 5, height: 4 },
    bubbleOffset = 12,
    wrapperGap = 4,
  } = props;
  const { colors } = useTheme();
  const tooltipRef = useRef<HTMLDivElement | null>(null);
  const [fixedPosition, setFixedPosition] = useState<
    React.CSSProperties | undefined
  >(undefined);
  useEffect(() => {
    const tooltipRect = tooltipRef.current?.getBoundingClientRect();
    const windowWidth = window.innerWidth ?? document.body.clientWidth;
    const windowHeight = window.innerHeight ?? document.body.clientHeight;
    if (tooltipRect?.width) {
      const fixedStyle: React.CSSProperties = {};
      // Horizontal boundary checks
      if (tooltipRect.left < 0) {
        fixedStyle.left = tooltipRect.left * -1 + gap;
      }
      if (tooltipRect.right < 0) {
        fixedStyle.right = tooltipRect.right * -1 + gap;
      }
      const diffRight = tooltipRect.left + tooltipRect.width - windowWidth;
      if (diffRight > 0) {
        fixedStyle.right = diffRight + gap * 2;
      }
      // Vertical boundary checks
      if (tooltipRect.top < 0) {
        fixedStyle.top = tooltipRect.top * -1 + gap;
      }
      if (tooltipRect.bottom < 0) {
        fixedStyle.bottom = tooltipRect.bottom * -1 + gap;
      }
      const diffBottom = tooltipRect.top + tooltipRect.height - windowHeight;
      if (diffBottom > 0) {
        fixedStyle.bottom = diffBottom + gap * 2;
      }
      if (Object.keys(fixedStyle).length > 0) {
        setFixedPosition(fixedStyle);
      }
    }
  }, []);
  // const { left, top } = useDropdownPosition(parent.current, tooltipPosition);
  if (!parent.current) return null;
  const parentRect = parent.current.getBoundingClientRect();
  const style = convertAnchor(
    parentRect,
    tooltipPosition,
    "center",
    bubbleSize.height + wrapperGap
  );
  let bubbleFixedLeft = 0;
  if (typeof fixedPosition?.left === "number") {
    bubbleFixedLeft = Math.abs(fixedPosition.left);
  }
  if (typeof fixedPosition?.right === "number") {
    bubbleFixedLeft = fixedPosition.right * -1;
  }
  return (
    <FixedWrapper style={style}>
      <TooltipWrapper
        ref={tooltipRef}
        style={{
          ...(props.style || {}),
          opacity: show ? 1 : 0,
          ...(fixedPosition || {}),
        }}
      >
        <Bubble
          width={bubbleSize.width}
          height={bubbleSize.height}
          $position={tooltipPosition}
          $fixedLeft={bubbleFixedLeft}
          $bubbleOffset={bubbleOffset}
          style={{ color: props.bubbleColor || colors.container200 }}
        />
        <>{tooltip}</>
      </TooltipWrapper>
    </FixedWrapper>
  );
}

const FixedWrapper = styled.div`
  pointer-events: none;
  position: fixed;
  z-index: ${(p) => p.theme.zIndexes.modal + 1};
`;

interface BubbleProps {
  $position: TooltipPosition;
  $fixedLeft: number;
  $bubbleOffset: number;
}

// Helper function to determine the base position for styling
const getBasePosition = (
  position: TooltipPosition
): "top" | "bottom" | "left" | "right" => {
  if (position.startsWith("top")) return "top";
  if (position.startsWith("bottom")) return "bottom";
  if (position.startsWith("left")) return "left";
  if (position.startsWith("right")) return "right";
  return position as "top" | "bottom" | "left" | "right";
};
const Bubble = styled(TooltipPoint)<BubbleProps>`
  position: absolute;

  ${(p) => {
    const basePosition = getBasePosition(p.$position);
    const isCompound = p.$position.includes("-");
    const offset = p.$bubbleOffset;
    switch (basePosition) {
      case "top":
        return css`
          ${() => {
            if (isCompound) {
              if (p.$position.endsWith("left")) {
                return `left: ${offset}px;`;
              } else {
                return `right: ${offset}px;`;
              }
            } else {
              return `left: calc(50% - ${p.$fixedLeft}px);`;
            }
          }}
          bottom: 0.5px;
          transform: ${() => {
            if (isCompound) {
              return "translate(0, 100%)";
            } else {
              return "translate(-50%, 100%)";
            }
          }};
        `;
      case "bottom":
        return css`
          ${() => {
            if (isCompound) {
              if (p.$position.endsWith("left")) {
                return `left: ${offset}px;`;
              } else {
                return `right: ${offset}px;`;
              }
            } else {
              return `left: calc(50% - ${p.$fixedLeft}px);`;
            }
          }}
          top: 0.5px;
          transform: ${() => {
            if (isCompound) {
              return "translate(0, -100%) rotate(180deg)";
            } else {
              return "translate(-50%, -100%) rotate(180deg)";
            }
          }};
        `;
      case "left":
        return css`
          ${() => {
            if (isCompound) {
              if (p.$position.endsWith("top")) {
                return `top: ${offset}px;`;
              } else {
                return `bottom: ${offset}px;`;
              }
            } else {
              return "top: 50%;";
            }
          }}
          right: 1.5px;
          transform: ${() => {
            if (isCompound) {
              return "translate(100%, 0) rotate(-90deg)";
            } else {
              return "translate(100%, -50%) rotate(-90deg)";
            }
          }};
        `;
      case "right":
        return css`
          ${() => {
            if (isCompound) {
              if (p.$position.endsWith("top")) {
                return `top: ${offset}px;`;
              } else {
                return `bottom: ${offset}px;`;
              }
            } else {
              return "top: 50%;";
            }
          }}
          left: 1.5px;
          transform: ${() => {
            if (isCompound) {
              return "translate(-100%, 0) rotate(90deg)";
            } else {
              return "translate(-100%, -50%) rotate(90deg)";
            }
          }};
        `;
      default:
        return css`
          left: calc(50% - ${p.$fixedLeft}px);
          top: 0.5px;
          transform: translate(-50%, -100%);
        `;
    }
  }}
`;

const TooltipWrapper = styled.div`
  width: fit-content;
  position: relative;
  ${(p) => p.theme.fonts.Default}
  ${(p) => p.theme.flex.row}
  ${(p) => p.theme.flex.center}
  z-index: ${(p) => p.theme.zIndexes.modal + 1};
  color: ${(p) => p.theme.colors.elementsContentMain};
  background-color: ${(p) => p.theme.colors.container200};
  gap: 6px;
  padding: 8px;
  border-radius: 8px;
  box-shadow: ${(p) => p.theme.colors.shadowPop};
`;
